@import "../../../css/module/color";

$left-width: 240px;

#leftSec {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	padding: 20px;
	overflow: auto;
	width: $left-width;
	border-right: 1px solid lighten($sidebar-bg, 5%);
	
	.cWrap {
		position: absolute;
		top: 0;
		bottom: 60px;
		overflow: auto;
	}
	#pButton {
		display: none;
		position: absolute;
		bottom: 20px;
	}
}

#rightSec {
	position: absolute;
	top: 0;
	left: $left-width;
	bottom: 0;
	right: 0;
	overflow: auto;
	padding: 2em;
	
	#photoManage {
		display: none;
	}
}

#photos {
	border: 1px solid $border-color;
	background-color: #fff;
	> li {
		display: inline-block;
		border: 1px solid $border-color;
		margin: 15px;
		img {
			display: block;
			max-width: 200px;
		}
		p {
			text-indent: 1em;
		}
	}
}